:root {
  --color-primary: #3b82f6;
  --color-seconday: #111827;
  --text-color: #4b5563;
  --color-secondary-light: #1f2937 --bg-section:#f9fafc;
  --heading-primary-font: 48px;
  --heading-secondary-font: 36px;
  --heading-tertiary-font: 24px;
  --heading-fourth-font: 2.2rem;
  --heading-fifth-font: 2rem;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.section-padding {
  padding: 9.4rem 25.4rem;
}

.tertiary-heading {
  font-size: var(--heading-secondary-font);
  color: var(--color-seconday);
  line-height: 3;
}

.heading-fourth {
  font-size: var(--heading-fourth-font);
  color: var(--color-seconday);
}

.heading-fifth {
  font-size: var(--heading-fifth-font);
  font-weight: 400;
}

.skill-icon {
  color: var(--color-primary);
  font-size: 5.6rem;
}

body {
  color: var(--text-color);
  font-family: Inter, sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  overflow-x: hidden;
}

.logo {
  border-radius: 50%;
  width: 10rem;
  height: 10rem;
}

.logo:hover {
  color: #2a27c0;
}

.top-nav {
  justify-content: space-between;
  align-items: center;
  padding: 2.4rem 4rem;
  font-size: 1.6rem;
  display: flex;
}

.top-nav-list {
  gap: 3rem;
  list-style: none;
  display: flex;
}

li a {
  color: inherit;
  text-decoration: none;
}

.header {
  text-align: center;
  background-color: var(--bg-section);
  object-fit: cover;
  object-position: top;
  padding: 7.2rem 0 12.4rem;
}

.header img {
  border-radius: 50%;
  width: 25rem;
  height: 25rem;
}

.header h1 {
  font-size: var(--heading-primary-font);
  color: var(--color-seconday);
}

.header h3 {
  font-size: var(--heading-tertiary-font);
  font-weight: 400;
}

.header h1, .header h3, .header p {
  margin-top: 1rem;
}

section.about-section p {
  line-height: 1.9;
}

section.skills-section {
  background-color: var(--bg-section);
}

.skills {
  flex-wrap: wrap;
  justify-content: space-between;
  row-gap: 3rem;
  list-style: none;
  display: flex;
}

.skill {
  background-color: #fff;
  border-radius: 1.5rem;
  flex-direction: column;
  gap: 2rem;
  width: 30%;
  margin-top: 3.5rem;
  padding: 2.4rem 3.2rem;
  display: flex;
  box-shadow: 0 1px 15px 2px #ddd;
}

.skill-title {
  font-size: var(--heading-fourth-font);
}

progress {
  appearance: none;
  width: 100%;
  height: 2rem;
  overflow: hidden;
}

progress::-webkit-progress-bar {
  background-color: #f3f4f6;
  border-radius: 1rem;
}

progress::-webkit-progress-value {
  background-color: var(--color-primary);
  border-radius: 1rem;
}

.experience {
  gap: 2.5rem;
  margin-top: 3rem;
  display: flex;
}

.experience-description {
  flex-direction: column;
  gap: 2rem;
  display: flex;
}

.line {
  background-color: var(--color-seconday);
  width: .3rem;
  position: relative;
}

.line:before {
  content: "";
  background-color: var(--color-primary);
  border-radius: 50%;
  width: 3rem;
  height: 3rem;
  position: absolute;
  top: 1rem;
  left: -50%;
  transform: translateX(-50%);
}

.date {
  color: var(--color-primary);
  font-weight: 500;
}

.contact-section {
  background-color: var(--bg-section);
}

.contact-form {
  width: 60%;
  margin: 5rem auto 0;
}

.input-box {
  flex-direction: column;
  gap: .5rem;
  margin-top: 2rem;
  display: flex;
}

.input-box > * {
  width: 100%;
}

.input-box label {
  font-weight: 400;
}

.input-box input, textarea {
  resize: none;
  border-radius: .6rem;
  padding: 1rem 2rem;
  font-size: 2rem;
}

.input-box input:focus, textarea:focus {
  outline: .1rem solid var(--color-primary);
}

.input-box input {
  border: none;
  height: 4rem;
}

textarea#message {
  height: 13rem;
}

.contact-btn {
  background-color: var(--color-primary);
  cursor: pointer;
  border: none;
  border-radius: 2rem;
  width: 100%;
  margin-top: 2rem;
  padding: 1rem;
  font-size: 2rem;
  font-weight: 200;
}

.contact-btn:hover {
  background-color: #7cfc00;
}

.footer {
  background-color: var(--color-seconday);
  padding: 3rem;
}

.all-right {
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-top: 1.5rem;
  display: flex;
}

footer .social-icons i {
  width: 4rem;
  height: 4rem;
  color: var(--bg-section);
  background-color: var(--color-seconday);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  display: flex;
}

.social-icons {
  justify-content: center;
  align-items: center;
  display: flex;
}

footer .social-icons i:hover {
  background-color: #a392e9;
}

@media only screen and (width <= 500px) {
  .top-nav {
    flex-direction: column;
    justify-content: end;
    align-items: center;
    display: flex;
  }

  .logo:hover {
    color: #2a27c0;
  }

  .top-nav-list {
    gap: 1.2rem;
  }

  .header {
    margin-bottom: 1rem;
    padding: 2rem 1rem 2rem 2rem;
  }

  .header img {
    border-radius: 50%;
    width: 20rem;
    height: 20rem;
  }

  .about-section {
    justify-content: center;
    align-items: center;
    padding: 3rem;
  }

  .skills-section {
    padding: 3.5rem;
  }

  .skills {
    grid-template-columns: 1fr;
    display: grid;
  }

  .skill {
    width: 100%;
  }

  .skill:hover {
    background-color: #37ee65;
  }

  .experience-section, .contact-section {
    padding: 3.5rem;
  }
}
/*# sourceMappingURL=myprofile.b06690c4.css.map */
